<template>
    <div class="content-wrap">
        <div class="topContent">
            <div class="title">订单信息</div>
            <div class="items">
                <div class="item">
                    <span>订单编号：</span>
                    <span>{{orderInfo.order_id ? orderInfo.order_id : '-'}}</span>
                </div>
                <div class="item">
                    <span>创建时间：</span>
                    <span>{{orderInfo.order_create_time | getTime}}</span>
                </div>
                <div class="item">
                    <span>下单时间：</span>
                    <span>{{orderInfo.order_create_time | getTime}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>订单状态：</span>
                    <span>{{orderInfo.order_status_str ? orderInfo.order_status_str : '-'}}</span>
                </div>
                <div class="item">
                    <span>下单人：</span>
                    <span>{{orderInfo.user_tel ? orderInfo.user_tel : '-'}}</span>
                </div>
                <div class="item">
                    <span>下单渠道：</span>
                    <span>{{orderInfo.client ? orderInfo.client : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>订购内容：</span>
                    <span>{{orderInfo.content ? orderInfo.content : '-'}}</span>
                </div>
                <div class="item">
                    <span>支付金额：</span>
                    <span>{{orderInfo.order_price ? ((orderInfo.order_price)/100).toFixed(2) : '-'}}</span>
                </div>
            </div>
            <div class="table-wrap">
                <el-table
                    :data="orderList"
                    :header-cell-style="{textAlign: 'center'}"
                    :cell-style="{ textAlign: 'center' }"
                    border
                    style="width: 100%">
                        <el-table-column
                            label="云机编号">
                            <template slot-scope="scope">
                                <span>{{scope.row&&scope.row.pad_codes ? scope.row.pad_codes : '-'}}</span>
                            </template>
                        </el-table-column>
                            
                        <el-table-column
                            prop="goods_name"
                            label="订购类别"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="content"
                            label="订购套餐">
                        </el-table-column>
                        <el-table-column
                            prop="pad_num"
                            label="订购数量">
                        </el-table-column>
                        <el-table-column
                            prop="group_name"
                            label="到期时间">
                        </el-table-column>
                        <el-table-column
                            label="RTMP URL">
                            <template slot-scope="scope">
                                <span>{{scope.row.rtmp_url ? scope.row.rtmp_url : '-'}}</span>
                            </template>
                        </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>
<script>
import { parseTime } from '../../utils/index';
import { mapState } from 'vuex'
export default {
    data() {
        return {
            orderInfo: {},
            orderList: []
        }
    },
    computed: {
        ...mapState({
            orders: state => state.order.orders
        }),
    },
    filters: {
        getTime(time) {
            if(time) return parseTime(time);
			return '-'
		},
    },
    mounted() {
        this.orderInfo = this.orders[0];
        this.orderList = this.orders;
        console.log(this.orders)
        // this.orderList.push(this.orderInfo);
    },
    methods: {
        
    }
}
</script>
<style lang="less" scoped>
.content-wrap{
    padding: 25px;
    .bottomContent{
        margin-top: 30px;
    }
    .topContent{
        padding: 20px 15px;
        border: 1px solid #eee;
        .title{
            border-left: 3px solid #409EFF;
            padding-left: 6px;
            margin-bottom: 20px;
        }
        .items{
            padding: 0 0 25px 6px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            .item{
                width: calc(100%/3)
            }
        }
    }
}
</style>


